@tailwind base;
@tailwind components;
@tailwind utilities;

.input {
  @apply p-3 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500;
}

@keyframes fade-in {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.animate-fade-in {
  animation: fade-in 0.6s ease-out both;
}